import React from 'react';
import { createRoot } from 'react-dom/client';

import Pager from './Pager';

const node = document.getElementById('counter2-container');

if (node) {
   const root = createRoot(node);
   root.render(
      <React.StrictMode>
         <Pager currentPage={10} totalPages={99} totalResults={2342} />
      </React.StrictMode>
   );
}
